import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ChildinputComponent} from '../childinput/childinput.component';
import { ChildioutputComponent} from '../childioutput/childioutput.component';
import { HomeComponent} from '../home/home.component';
import { AboutComponent} from '../about/about.component';
import { ContactComponent} from '../contact/contact.component';
import { ServicesComponent } from '../services/services.component';

const routes: Routes = [
{path:'first', component: ChildinputComponent},
{path: 'second', component:ChildioutputComponent},
{path: 'a', component:HomeComponent},
{path: 'b', component:AboutComponent},
{path: 'c', component:ContactComponent},
{path: 'd', component:ServicesComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class RoutingmoduleRoutingModule { }
